import React from "react";
import PokeCard  from "./PokeCard";
import {allPokemonsData} from "./allPokemonsData";
import "./App.css";


class App extends React.Component{
    constructor(){
        super()
        this.state = {
            limit: 24,
            sortBy:"id",
            filterBy: "all",
            secondFilterBy : "all",
            userInput:"",

            allPokemons : [...allPokemonsData],
            sortedPokemons: [],
            filteredAndSortedPokemons: [],
        }
    }


    componentDidMount=()=>{
        this.sortPokemons();
    }


//------------------------------------------------------------------------SORTING-----------------------------------------------
    setSortValue = (e)=>{
        this.setState({
            sortBy: e.target.value
        },()=>{
            this.sortPokemons();
        })
    }

    sortPokemons = ()=>{
        const allPokemons = [...this.state.allPokemons];
        if(this.state.sortBy === "id"){
            allPokemons.sort((a,b)=>{
                if(a.id > b.id){
                    return 1;
                }else{
                    return -1;
                }
            })
        }
        if(this.state.sortBy === "heavy"){
            allPokemons.sort((a,b)=>{
                if(a.weight < b.weight){
                    return 1;
                }else{
                    return -1;
                }
            })
        }
        if(this.state.sortBy === "light"){
            allPokemons.sort((a,b)=>{
                if(a.weight > b.weight){
                    return 1;
                }else{
                    return -1;
                }
            })
        }
        if(this.state.sortBy === "biggest"){
            allPokemons.sort((a,b)=>{
                if(a.height < b.height){
                    return 1;
                }else{
                    return -1;
                }
            })
        }
        if(this.state.sortBy === "smallest"){
            allPokemons.sort((a,b)=>{
                if(a.height > b.height){
                    return 1;
                }else{
                    return -1;
                }
            })
        }
        this.setState({
            sortedPokemons : allPokemons
        },()=>{
            this.filterPokemons();
        })
    }

    // -----------------------------------------------------------------------------------FILTERING----------------------------------------------------
    setFilter=(e)=>{
        if(e.target.value === "all"){
            this.setState({
                secondFilterBy: "all"
            })
        }
        this.setState({
            filterBy: e.target.value,
        },()=>{
            this.filterPokemons();
        })
    }

    filterPokemons = ()=>{
        const sortedPokemons = [...this.state.sortedPokemons];
        let filteredPokemons = []
        if(this.state.filterBy === "all"){
            filteredPokemons = sortedPokemons;
        }else{
            filteredPokemons = sortedPokemons.filter((pokemon)=>{
                const type1 = pokemon.type[0];
                const type2 = pokemon.type[1];
                return type1 === this.state.filterBy || type2 === this.state.filterBy;
            })
            if(this.state.secondFilterBy !== "all"){
            filteredPokemons = filteredPokemons.filter((pokemon)=>{
                const type1 = pokemon.type[0];
                const type2 = pokemon.type[1];
                return type1 === this.state.secondFilterBy || type2 === this.state.secondFilterBy;
            })
        }
        }

        

        this.setState({
            filteredAndSortedPokemons : filteredPokemons
        })
    }


    // ----------------------------------------------------------------------------------SEARCHING AND RENDER----------------------------------------------------------
    setUserInput=(e)=>{
        this.setState({
            userInput: e.target.value,
        })
    }
  
    renderCards = ()=>{
        const all = [...this.state.filteredAndSortedPokemons];
        const filteredByUserInput = all.filter((pokemon)=>{
            return pokemon.name.toLowerCase().includes(this.state.userInput.toLowerCase())
        })
        const limited = filteredByUserInput.splice(0,this.state.limit)
        return limited.map((pokemon)=>{
            return <PokeCard key={pokemon.id} pokemon={pokemon}/>
        })
    }
    //------------------------------------------------------------------------------------ADD SECOND TYPE--------------------------------------------------------------

    renderSecondType = ()=>{
        if(this.state.filterBy !== "all"){
            return <select id="2" onChange={this.setSecondFilterBy}>
            <option disabled selected>Second Type</option>
            <option value="all" defaultValue  >All</option>
            <option value="normal">Normal</option>
            <option value="grass">Grass</option>
            <option value="fire">Fire</option>
            <option value="water">Water</option>
            <option value="fighting">Fighting</option>
            <option value="flying">Flying</option>
            <option value="poison">Poison</option>
            <option value="ground">Ground</option>
            <option value="rock">Rock</option>
            <option value="bug">Bug</option>
            <option value="ghost">Ghost</option>
            <option value="electric">Electric</option>
            <option value="psychic">Psychic</option>
            <option value="ice">Ice</option>
            <option value="dragon">Dragon</option>
            <option value="dark">Dark</option>
            <option value="steel">Steel</option>
            <option value="Fairy">Fairy</option>
        </select>
        }
    }

    setSecondFilterBy=(e)=>{
        this.setState({
            secondFilterBy : e.target.value
        },()=>{
            this.filterPokemons();
        })
    }

    //-----------------------------------------------------------------------------------LIMIT REDNER-------------------------------------------------------------------
    loadMore=()=>{
        this.setState({
            limit: this.state.limit + 24
        })
    }
    detectScrollBottom = ()=>{
        window.onscroll = (e)=> {
            if ((window.innerHeight + window.scrollY) >= document.body.offsetHeight) {
                if(this.state.allPokemons.length > this.state.limit){
                    this.loadMore();
                }
            }
        };
    }



    render(){
    //increasing the render limit
    this.detectScrollBottom()

            return(
                <div style={{textAlign:"center"}}>
                    <img id="logo" src="./img/pokeSearchLogo.png" alt="PokeSearchLogo"></img>
                    <div className="searchDiv">
                        {/* search----------------------------------- */}
                        <input value={this.state.userInput} onChange={this.setUserInput} id="searchbar" type="text" placeholder="Search Pokemon..." />

                        <select id="1" onChange={this.setFilter}>
                            <option disabled selected>First Type</option>
                            <option value="all" defaultValue >All</option>
                            <option value="normal">Normal</option>
                            <option value="grass">Grass</option>
                            <option value="fire">Fire</option>
                            <option value="water">Water</option>
                            <option value="fighting">Fighting</option>
                            <option value="flying">Flying</option>
                            <option value="poison">Poison</option>
                            <option value="ground">Ground</option>
                            <option value="rock">Rock</option>
                            <option value="bug">Bug</option>
                            <option value="ghost">Ghost</option>
                            <option value="electric">Electric</option>
                            <option value="psychic">Psychic</option>
                            <option value="ice">Ice</option>
                            <option value="dragon">Dragon</option>
                            <option value="dark">Dark</option>
                            <option value="steel">Steel</option>
                            <option value="Fairy">Fairy</option>
                        </select>
                        {
                            //render second option if first set
                            this.renderSecondType()
                        } 
                        <select onChange={this.setSortValue} id="filter">
                            <option disabled selected >Sort By</option>
                            <option value="id" >Id</option>
                            <option value="heavy" >Heavy</option>
                            <option value="light" >Light</option>
                            <option value="biggest">Biggest</option>
                            <option value="smallest">Smallest</option>
                        </select>
                    </div>

                    <div className="container">
                    {/* renering the cards from state */}
                        {this.renderCards()}
                    </div>
                </div>
            )
    }
}


export default App;





