import React from "react";
import "./PokeCard.css";



const PokeCard = ({pokemon})=>{
    function capitalizeFirstLetter(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
    }
    return(
        <div className="card">
            <h3>{pokemon.name}</h3>
            <div className="pokeImgContainer">
                <img className="pokeImg" src={`https://pokeres.bastionbot.org/images/pokemon/${pokemon.id}.png`} alt = {pokemon.id} />
            </div>
            <div className="typeContainer">               
                {
                    pokemon.type.map((type)=>{
                        return  <img title={capitalizeFirstLetter(type)} key={type} className="typeIcon" src={`./img/pokeTypes/${type}.png`} alt={type}/>
                    })
                }
            </div>
     

            <div className="infoContainer" >
                <div className="info">Weight</div>
                <div className="value">{Math.round(pokemon.weight/2.205)}kg</div>
            </div>
            <div className="infoContainer">
                <div className="info">Height</div>
                <div className="value">{Math.round(pokemon.height/3.281)}m</div>
            </div>
        </div>
    )
}



export default PokeCard;